body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  font-family: "Arial";
  font-size: large;
  padding-top: 1rem;
}

.App {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  padding-left: 10%;
  padding-right: 10%;
}

.ExerciseBank {
  width: 50rem;
  background-color: lightgray;
  padding: 5%;
  margin-right: 5%;
}

.CurrentSession {
  width: 50rem;
  background-color: lightgray;
  padding: 5%;
  margin-left: 5%;
}

.Exercise {
  background-color: grey;
  margin-bottom: 5%; 
  padding: 3rem;
  justify-content: center;
  align-content: center;
  border-radius: 1rem;
}

.ExerciseList *:not(:last-child) {
  display: block;
}

.ExerciseListCurrent *:not(:last-child) {
  display: block;
}

.Buttons{
  display: flex;
  justify-content: center;
  padding: 0 1rem 1rem 1rem;
  margin: 0 1rem 1rem 1rem;
}

.Buttons button {
  padding: 1rem;
  width: 5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.EditButtonC {
  display: block;
  padding-top: 20%;
  width:20rem;
}



.AddExerciseContainer {
  align-content: center;
  display: block;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

#ExerciseName {
  align-content: center;
  justify-content: center;
  width: 20rem;
  text-align: center;
  margin: auto;
}

.ExerciseNameForm {
  text-align: center;
  
}

input[type="text"]
{
  font-family: "Arial";
}